.page-article {
  padding: 14px;
}
.page-article .title {
  font-size: 24px;
  line-height: 34px;
  font-weight: normal;
  margin-bottom: 12px;
}
.page-article .info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 24px;
}
.page-article .info .user-info {
  display: flex;
  align-items: center;
}
.page-article .info .user-info img {
  height: 24px;
  border-radius: 50%;
  margin-right: 10px;
}
.page-article .info .user-info .u-name {
  font-size: 14px;
  font-weight: bolder;
}
.page-article .info .user-info .u-name.vip {
  padding-right: 20px;
  background: url(data:image/png;base64,) no-repeat right center;
  background-size: 14px;
}
.page-article .info .p-info {
  font-size: 12px;
  color: #999;
}
.page-article .content {
  position: relative;
  overflow: hidden;
  clear: both;
}
.page-article .content p {
  word-wrap: break-word;
  color: #222;
  text-align: justify;
  font-size: 18px;
  line-height: 1.76;
  margin-top: 17px;
}
.page-article .content a {
  color: #5784df;
}
.page-article .content img {
  width: 100%;
  display: block;
  margin-top: 4px;
}
.page-article .content h1 {
  font-size: 18px;
  border-left-width: 3px;
  border-left: solid #ed4040;
  padding-left: 6px;
  line-height: 28px;
  margin: 24px 0;
}
.page-article .content blockquote {
  font-size: 16px;
  line-height: 22px;
  padding: 15px;
  color: #999;
  border: 1px solid #E8E8E8;
  background: #F4F5F6;
  margin-top: 17px;
}
